import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import UserStart from './views/UserStart.vue'
// import UserDetail from './views/UserDetail.vue'
// import UserEdit from './views/UserEdit.vue'
import HeaderTop from './views/HeaderTop.vue'


Vue.use(Router)

const vueRouter = new Router({
  mode: 'history',
  // hash模式(默认)：#/xx    不需要和后端沟通
  // history模式：没有#   真实的去后台取当前url路由 比较美观 后面乱输入值要和后端沟通设置
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      // component: Home
      components: {
        default: Home,
        'headertop': HeaderTop,
      }
    },
    {
      path: '/about',
      name: 'about',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    },
    {
      // 动态路由
      path: '/user',
      name: 'user',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      // 异步加载：
      component: () => import(/* webpackChunkName: "about" */ './views/User.vue'),
      children: [
        {

          path: '',//不能加写
          component: () => import(/* webpackChunkName: "about" */ './views/UserStart.vue'),

        },
        {
          path: ':id',
          component: () => import(/* webpackChunkName: "about" */ './views/UserDetail.vue'),
          // 2.局部路由守卫
          beforeEnter(to, from, next) {
            console.log("局部路由");
            next()
          },

        },
        {
          name: 'userEdit',
          path: ':id/edit',
          component:() =>import(/* webpackChunkName: "about" */ './views/UserEdit.vue'),
        },

      ],
    },
    {

      path: '*',
      redirect: { name: 'home' }

    }
  ],
  // vue-router的滚动行为的函数 
  // PS:!!!!!!!!!!只要添加了过渡动画，锚点就失效
  //savedPosition：通过浏览器的前进后退，上个页面滚动条位置
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    }
    if (to.hash) {//锚点
      return {
        selector: to.hash
      }
    }
    return { x: 0, y: 0 }
  }
})
// 全局的导航守卫:退出提醒，登陆验证
//进入路由前都会执行的函数
vueRouter.beforeEach((to, from, next) => {
  console.log("全局-beforeEach");
  next()//不写不会进行下一步 
  //next(false)相当于不写next
  //next():该干嘛干嘛
  //next('/xxx')字符串或者对象  重定向到另个路由 ，e.g 登陆验证调到登录页面

  // 全局后置钩子，然而和守卫不同的是，这些钩子不会接受 next 函数也不会改变导航本身：
  vueRouter.afterEach((to, from) => {
    console.log("全局-afterEach");
    // console.log(to,from);


  })
})
export default vueRouter

